





























































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class AccountForm extends Vue {
  action: string = "add";
  genderText: string = "";
  userAccount: string = "";
  firstName: string = "";
  lastName: string = "";
  permissionText: string = "";
  gender: Object = ["男", "女", "其他", "不願提供"];
  permission: Object = ["醫生", "護理師"];
  clear() {
    this.genderText = "";
    this.userAccount = "";
    this.firstName = "";
    this.lastName = "";
    this.permissionText = "";
  }
}
